import React from 'react';

const Proyecto = (props) => {
    const alt = `Imagen de proyecto ${props.proyecto.nombre}`;
    return (
        <div className="carta carta-hover">
            <h3>{props.proyecto.nombre}</h3>
            <div className="contenedor-logo">
                <img src={props.proyecto.img} alt={alt} className="logo-proyectos"/>
            </div>
            <p>{props.proyecto.descripcion}</p>
            <a href={props.proyecto.url} target="_blank" rel="noopener noreferrer" className="btn">Visitar</a>
        </div>
    );
};

export default Proyecto;