import React, { Component } from 'react';
import Servicio from './Servicio';

class Servicios extends Component {
    render() {
        return (
            <React.Fragment>
                <h2>Servicios</h2>
                <article>
                    {this.props.servicios.map(servicio => (
                        <Servicio
                            key={servicio.nombre}
                            servicio={servicio}
                        />
                    ))}
                </article>
            </React.Fragment>
        );
    }
}

export default Servicios;