import React from 'react';

const Aprendizaje = props => {
    return (
        <React.Fragment>
            <h2>Aprendizaje</h2>
            <article>
                {props.aprendizaje.map(aprendizaje => (
                    <div className="carta carta-hover" key={aprendizaje.url}>
                        <h3>{aprendizaje.nombre}</h3>
                        <img src={aprendizaje.img} alt={aprendizaje.nombre} className="logo-proyectos"/>
                        <p>{aprendizaje.descripcion}</p>
                        <a href={aprendizaje.url} target="_blank" rel="noopener noreferrer" className="btn">Ver certificado</a>
                    </div>
                ))}
            </article>
        </React.Fragment>
    );
};

export default Aprendizaje;