import React from 'react';

const Servicio = props => {
    const alt = `Imagen de servicio ${props.servicio.nombre}`;

    return (
        <div className="carta carta-hover">
            <h3>{props.servicio.nombre}</h3>
            <img src={props.servicio.img} alt={alt} className="logo-proyectos"/>
            <p>{props.servicio.descripcion}</p>
            <a href={props.servicio.url} target={props.servicio.target} rel="noopener noreferrer" className="btn">Solicitar</a>
        </div>
    );
};

export default Servicio;