import React from 'react';
import harold from '../img/harold.jpg';

const Header = (props) => {
    return (
        <header>
            <nav>
                <ul>
                    <li><a target="_blank" rel="noopener noreferrer" href="https://www.facebook.com/Harverbo"><i className="fab fa-facebook-f"></i></a></li>
                    <li><a target="_blank" rel="noopener noreferrer" href="https://twitter.com/HaroldAHatake"><i className="fab fa-twitter"></i></a></li>
                    <li><a target="_blank" rel="noopener noreferrer" href="https://www.instagram.com/haroldhatake/"><i className="fab fa-instagram"></i></a></li>
                    <li><a target="_blank" rel="noopener noreferrer" href="https://www.linkedin.com/in/harverbo/"><i className="fab fa-linkedin-in"></i></a></li>
                    <li><a target="_blank" rel="noopener noreferrer" href="https://www.fiverr.com/harverbo"><i className="fiverr">Fi</i></a></li>
                </ul>
            </nav>
            <div className="foto">
                <img src={harold} alt="Foto de Harold"/>
            </div>
            <h1>{props.titulo}</h1>
            <h2>
                <span>Desarrollador Web BackEnd</span>
                <span>(PHP <i className="fab fa-php"></i> - Laravel <i className="fab fa-laravel"></i> - ReactJS <i className="fab fa-react"></i>) - WordPress <i class="fab fa-wordpress-simple"></i></span>
            </h2>
        </header>
    );
};

export default Header;