import React, { Component } from 'react';
import Header from './Header';
import Proyectos from './Proyectos';
import Servicios from './Servicios';
import Aprendizaje from './Aprendizaje';
import Contacto from './Contacto';

import axios from 'axios';

class App extends Component {
  state = {
    proyectos: [],
    servicios: [],
    aprendizaje: [],
  }

  componentDidMount() {
    this.cargarProyectos();
  }

  cargarProyectos = async () => {
    let respuesta;

    respuesta = await axios.get('https://my-json-server.typicode.com/Harverbo/portafolio/Proyectos');
    let proyectos = respuesta.data;

    respuesta = await axios.get('https://my-json-server.typicode.com/Harverbo/portafolio/Servicios');
    let servicios = respuesta.data;

    respuesta = await axios.get('https://my-json-server.typicode.com/Harverbo/portafolio/Aprendizaje');
    let aprendizaje = respuesta.data;

    this.setState({
      proyectos,
      servicios,
      aprendizaje,
    });
  }

  render(){
    return (
      <div className="App">
        <Header
          titulo='Harold Abreu Ramírez'
        />

        <Proyectos
          proyectos = {this.state.proyectos}
        />

        <Servicios
          servicios = {this.state.servicios}
        />

        <Aprendizaje
          aprendizaje = {this.state.aprendizaje}
        />

        <Contacto/>
      </div>
    );
  }
}

export default App;
