import React, { Component } from 'react';
import Proyecto from './Proyecto';

class Proyectos extends Component {
    render() {
        return (
            <React.Fragment>
                <h2>Proyectos</h2>
                <article>
                    {this.props.proyectos.map(proyecto => (
                        <Proyecto
                            key = {proyecto.nombre}
                            proyecto = {proyecto}
                        />
                    ))}
                </article>
            </React.Fragment>
        );
    }
}

export default Proyectos;