import React, {Component} from 'react';
import Error from './Error';
import Success from './Success';
import '../css/Spinner.css';

class Contacto extends Component {
    state = {
        error: false,
        success: false,
        mensaje: ''
    }

    nombreRef = React.createRef();
    correoRef = React.createRef();
    mensajeRef = React.createRef();

    contactMsg = (e) => {
        e.preventDefault();

        const datos = {
            nombre: this.nombreRef.current.value,
            correo: this.correoRef.current.value,
            mensaje: this.mensajeRef.current.value
        }

        if(datos.nombre === '' || datos.correo === '' || datos.mensaje === ''){
            this.setState({
                error: true,
                mensaje: 'Todos los campos son obligatorios'
            }, () => {
                setTimeout(() => {
                    this.setState({
                        error:false,
                        mensaje: ''
                    });
                }, 3000);
            });

            return;
        }

        var templateParams = {
            user_name: datos.nombre,
            user_email: datos.correo,
            text: datos.mensaje
        };
        var service_id = "default_service";
        var template_id = "harverbo";

        document.querySelector('.spinner').classList.remove('d-none');

        window.emailjs.send(service_id, template_id, templateParams)
            .then((response) => {
                document.querySelector('.spinner').classList.add('d-none');

                this.setState({
                    success: true,
                    mensaje: `Su correo ha sido enviado. Pronto me pondré en contacto con usted desde el correo ${datos.correo}`
                }, () => {
                    setTimeout(() => {
                        this.setState({
                            success: false,
                            mensaje: ''
                        });
                    }, 5000);
                });

            }, (error) => {
                document.querySelector('.spinner').classList.add('d-none');

                this.setState({
                    error: true,
                    mensaje: "Debe comprobar que no es un robot"
                }, () => {
                    setTimeout(() => {
                        this.setState({
                            error: false,
                            mensaje: ''
                        });
                    }, 3000);
                });
            });
    }

    render(){
        const mensajeError = (this.state.error) ? <Error mensaje={this.state.mensaje} /> : '';
        const mensajeSuccess = (this.state.success) ? <Success mensaje={this.state.mensaje} /> : '';

        return (
            <React.Fragment>
                <h2>Contacto</h2>
                <article id="contacto">
                    <div className="carta">
                        {mensajeError}
                        {mensajeSuccess}
                        <form onSubmit={this.contactMsg}>
                            <div className="input-field">
                                <label htmlFor="nombre">Nombre</label>
                                <input className="form-control" ref={this.nombreRef} type="text" id="nombre" placeholder="Nombre de pila" />
                            </div>

                            <div className="input-field">
                                <label htmlFor="correo">Correo</label>
                                <input className="form-control" ref={this.correoRef} type="email" id="correo" placeholder="Correo electrónico"/>
                            </div>

                            <div className="input-field">
                                <label htmlFor="mensaje">Mensaje</label>
                                <textarea className="form-control" ref={this.mensajeRef} id="mensaje" cols="30" rows="10" placeholder="Mensaje"></textarea>
                            </div>

                            <div className="g-recaptcha" data-sitekey="6Lc-y6EUAAAAAGPb6jr4SabxXsZ2rhRAHZ5AAJol"></div>

                            <div className="input-field input-btn">
                                <input type="submit" className="btn" value="Enviar"/>

                                <div className="spinner d-none">
                                    <div className="rect1"></div>
                                    <div className="rect2"></div>
                                    <div className="rect3"></div>
                                    <div className="rect4"></div>
                                    <div className="rect5"></div>
                                </div>
                            </div>
                        </form>
                    </div>
                </article>
            </React.Fragment>
        );
    }
};

export default Contacto;